"use client";

import { useState } from "react";
import Link from "next/link";
import Image from "next/image";
import { ArrowUpRight } from "lucide-react";

type CategoryKey = "technology" | "experience" | "strategy" | "growth";

const categories = {
  technology: {
    title: "Technology",
    description:
      "We are a leader in building cutting-edge mobile and web applications that are AI-driven and intuitive digital solutions. We push boundaries, solve complex challenges through products that drive real impact.",
    services: [
      "Mobile App Development",
      "Web Development",
      "AI & Innovation",
      "Backend & Infrastructure",
      "Emerging Technology",
    ],
  },
  experience: {
    title: "Experience",
    description:
      "We craft exceptional user experiences through deep research, thoughtful UX design, and polished visual execution that resonates with your audience.",
    services: [
      "Product Vision",
      "User Research",
      "UX Design",
      "Visual Design",
      "Design Systems",
    ],
  },
  strategy: {
    title: "Strategy",
    description:
      "We align technology with business goals through strategic planning, market insights, and technical architecture that set the foundation for long-term success.",
    services: [
      "Digital Strategy",
      "Product Roadmapping",
      "Market & Competitive Analysis",
      "Technical Architecture Planning",
      "Innovation Consulting",
    ],
  },
  growth: {
    title: "Growth",
    description:
      "We help products scale sustainably with performance optimization, data-driven decisions, and infrastructure built to support growing demand.",
    services: [
      "Product Optimization",
      "Performance & Analytics",
      "Conversion Rate Optimization",
      "Scaling Infrastructure",
      "Long-term Product Support",
    ],
  },
};

const categoryOrder: CategoryKey[] = ["technology", "experience", "strategy", "growth"];

export default function WhatWeDoSection() {
  const [activeCategory, setActiveCategory] = useState<CategoryKey>("technology");

  const activeIndex = categoryOrder.indexOf(activeCategory);

  return (
    <section className="relative bg-black text-white py-24 md:py-32 overflow-hidden">
      {/* Curved black overlay */}
      <div className="absolute right-0 top-0 bottom-0 w-full md:w-1/2 bg-black">
        <svg className="absolute left-0 h-full w-32 md:w-64" viewBox="0 0 100 100" preserveAspectRatio="none">
          <path d="M100,0 C0,0 0,100 100,100 L100,0 Z" fill="black" />
        </svg>
      </div>

      {/* Background Image */}
      <div className="absolute right-0 top-0 bottom-0 w-full md:w-1/2">
        <Image
          src="/Futuristic side view portrait of young african-american woman wearing vr headset against purple background _ Premium Photo.jpeg"
          alt="Innovative tech development"
          fill
          className="object-cover"
          priority
        />
      </div>

      <div className="relative max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="grid md:grid-cols-2 items-center gap-12">
          {/* Left Column: Categories */}
          <div className="space-y-12">
            <p className="text-lg text-white/70">What we do</p>

            <h2 className="text-5xl md:text-6xl lg:text-7xl font-bold leading-tight">
              Elegant solutions built on proven methodologies.
            </h2>

            {/* Interactive Category List */}
            <div className="relative space-y-12">
              {/* Vertical Indicator Line */}
              <div
                className="absolute left-0 top-0 w-0.5 bg-white/30 transition-transform duration-500 ease-out"
                style={{
                  height: "calc(100% - 3rem)",
                  transform: `translateY(${activeIndex * 100}%)`,
                }}
              />

              {categoryOrder.map((key, index) => {
                const cat = categories[key];
                const isActive = activeCategory === key;

                return (
                  <div
                    key={key}
                    className="relative pl-8"
                    onMouseEnter={() => setActiveCategory(key)}
                    onClick={() => setActiveCategory(key)} // Mobile/touch support
                  >
                    {/* Active Indicator Dot */}
                    {isActive && (
                      <div className="absolute left-0 top-2 w-2 h-2 bg-white rounded-full transition-all duration-300" />
                    )}

                    <h3
                      className={`text-3xl md:text-4xl font-bold flex items-center gap-4 transition-all duration-300 cursor-pointer group ${
                        isActive ? "text-white" : "text-white/50"
                      }`}
                    >
                      <span className={isActive ? "text-white/70" : "text-white/30"}>
                        {String(index + 1).padStart(2, "0")}
                      </span>
                      {cat.title}
                      <ArrowUpRight
                        className={`w-6 h-6 transition-all duration-300 ${
                          isActive
                            ? "translate-x-1 -translate-y-1 opacity-100"
                            : "translate-x-0 translate-y-0 opacity-0 group-hover:opacity-50"
                        }`}
                      />
                    </h3>

                    {/* Description - only shown for Technology in static version */}
                    {key === "technology" && !isActive && (
                      <p className="text-lg text-white/80 max-w-md mt-4">
                        {cat.description}
                      </p>
                    )}
                  </div>
                );
              })}
            </div>
          </div>

          {/* Right Column: Dynamic Services & Description */}
          <div className="md:pl-20 space-y-12">
            <div className="transition-opacity duration-500 ease-out">
              <p className="text-lg text-white/90 mb-8 max-w-md">
                {categories[activeCategory].description}
              </p>

              <ul className="space-y-4 text-lg text-white/90">
                {categories[activeCategory].services.map((service) => (
                  <li key={service} className="transition-opacity duration-300">
                    {service}
                  </li>
                ))}
              </ul>
            </div>

            <Link
              href="/services"
              className="inline-block bg-transparent border-2 border-white/50 text-white px-8 py-4 rounded-full font-medium hover:bg-white/10 transition duration-300"
            >
              Services
            </Link>
          </div>
        </div>
      </div>
    </section>
  );
}